const _triggers= new Map;

/**
 * 프로세스 진행을 보류/관리 합니다. 프로세스의 진행 상태를 제어 및 제어권을 반환 하는 형태의 코루틴을 제공 합니다.
 * @namespace GraftSuspend
 * @property {function} has `(key: any)=> boolen`<br/>
 * 프로제스 제어자 등록 여부를 조회 합니다.<br/>
 * 👉 {@link GraftSuspend.has}
 * @property {function} run `(key: any, onInit?: ()=> void)=> Promise<any>`<br/>
 * 프로제스 제어자를 등록 및 실행 대기 합니다.<br/>
 * 👉 {@link GraftSuspend.run}
 * @property {function} resolve `(key: any, receive?: any)=> void`<br/>
 * 프로세스 실행 대기를 귀결 처리 합니다.<br/>
 * 👉 {@link GraftSuspend.resolve}
 * @property {function} reject `(key: any, reason?: any)=> void`<br/>
 * 프로세스 실행 대기를 거부 처리 합니다.<br/>
 * 👉 {@link GraftSuspend.reject}
 */
module.exports= _graft=> {
    /**
     * {@link GraftSuspend}<br>
     * 프로제스 제어자 등록 여부를 조회 합니다.
     * @memberOf GraftSuspend
     * @typedef {function} has
     * @param {any} key 조회키
     * @returns {boolean}
     * @example
     * // 등록 여부 조회
     * const hasXXX= graft.suspend.has('xxx');
     * if(hasXXX) graft.log.out('등록');
     */
    const has= ky=> _triggers.has(ky);
    /**
     * {@link GraftSuspend}<br>
     * 프로세스 제어자를 등록 및 실행 대기 합니다.
     * 프로세스의 등록 및 실행은 프로세스 제어자가
     * 반환(귀결: {@link GraftSuspend.resolve} / 거부: {@link GraftSuspend.reject})되기 전까지 재등록 할 수 없습니다.
     * @memberOf GraftSuspend
     * @typedef {function} run
     * @param {any} key 등록키
     * @param {function=} onInit 수행 기능 필요시 기능을 구현 합니다.
     * @returns {Promise<any>}
     * @example
     * // 코루틴 등록 및 프로세스 대기
     * graft.suspend.run('xxx')
     *      .then(receive=> {
     *          // 코루틴 수행 귀결 처리 결과를 반환 받습니다.
     *          graft.log.out('정상', receive);
     *      })
     *      .catch(reason=> {
     *          // 코루틴 수행 거부 처리 결과를 반환 받습니다.
     *          graft.log.out('오류', reason);
     *      });
     */
    const run= (ky, proc)=> {
        if(_triggers.has(ky)) {
            console.warn('제어자가 중복 됩니다.', new Error);
            return Promise.reject('제어자가 중복 됩니다.');
        }

        return new Promise((resolve, reject)=> {
            _triggers.set(ky, {resolve, reject});
            proc&& proc();
        });
    };
    /**
     * {@link GraftSuspend}<br>
     * 프로세스 실행 대기를 귀결 처리 하며,
     * {@link GraftSuspend.run}을 실행 대기중인 프로세스로 돌아 갑니다.
     * @memberOf GraftSuspend
     * @typedef {function} resolve
     * @param {any} key 조회키
     * @param {any=} receive 귀결 처리에 대한 반환값
     * @example
     * // 코루틴 정상 반환
     * // run('xxx').then 메소드를 통해 반환 받습니다.
     * graft.suspend.resolve('xxx', {});
     */
    const resolve= (ky, receive)=> {
        const trigger= _triggers.get(ky);
        if(trigger) {
            _triggers.delete(ky);
            trigger.resolve(receive);
        }
    };
    /**
     * {@link GraftSuspend}<br>
     * 프로세스 실행 대기를 거부 처리 하며,
     * {@link GraftSuspend.run}을 실행 대기중인 프로세스로 돌아 갑니다.
     * @memberOf GraftSuspend
     * @typedef {function} reject
     * @param {any} key 조회키
     * @param {any=} reason 거부 처리 사유
     * @example
     * // 코루틴 거부 반환
     * // run('xxx').catch 메소드를 통해 반환 받습니다.
     * graft.suspend.reject('xxx', {});
     */
    const reject= (ky, reason)=> {
        const trigger= _triggers.get(ky);
        if(trigger) {
            _triggers.delete(ky);
            trigger.reject(reason);
        }
    };

    return {has, run, resolve, reject};
};
