/**
 * {@link GraftBridge}<br>
 * 네이티브와 웹 매체간의 주고 받는 메시지 구조체 입니다.<br/>
 * @memberOf GraftBridge
 * @typedef {object} message
 * @property {string} trid 웹에서 채번된 거래번호로 웹에서는 직접 사용하지 않음
 * @property {string=} command 네이트브로 전달 할 기능 명령어
 * @property {object=} payload 네이트브로 전달 할 파라미터
 * @property {object=} receive 네이트브에서 받은 결과
 * @example
 * // Web에서 네이트브로 전달된 메시지 구조
 * // 네이티브에서 수행할 기능을 command를 이용하여 호출 하며,
 * // 전달할 인자가 있는 경우 payload를 사용 합니다.
 * {
 *     trid: 'w112233',
 *     command: 'cmd_sample',
 *     payload: {}
 * }
 * 
 * // 네이트브에서 Web으로 응답할 결과 메시지 구조
 * // 웹으로 부터 받은 command 수행 후,
 * // Graft.postMessage 메소드를 이용하여 수행 완료 처리 합니다.
 * // 수행완료 시 웹으로 부터 받은 trid와 receive를 조합하여 인자로 전달 합니다.
 * Graft.postMessage({
 *     trid: 'w112233',
 *     receive: {}
 * });
 */
const _msgMap= {
    self: new Map,
    getTrid(trigger) {
        const trid= 'w'+ (new Date().getTime()+ this.self.size);
        this.self.set(trid, trigger);
        return trid;
    },
    getTrigger(trid) {
        const trigger= this.self.get(trid);
        this.self.delete(trid);
        return trigger|| {};
    }
};
const _native= {
    self: undefined,
    useNative() { return !!this.self },
    postNative(msg) { this.self.postMessage(Object.assign({payload: {}}, msg)) },
};
const _mock= new Map;


/**
 * 네이티브와 메시지를 주고 받는 역할을 합니다.
 * @namespace GraftBridge
 * @property {function} setNative `()=> any`<br/>
 * 네이티브 객체를 세팅합니다.<br/>
 * 👉 {@link GraftBridge.setNative}
 * @property {function} postMessage `()=> any`<br/>
 * 네이티브에 메시지를 전달 합니다.<br/>
 * 👉 {@link GraftBridge.postMessage}
 * @property {object} mock 네이티브 목객체.<br/>
 * 👉 {@link GraftBridge.mock}
 */
module.exports= _graft=> {
    /**
     * {@link GraftBridge}<br>
     * 네이티브 객체를 세팅합니다.
     * @memberOf GraftBridge
     * @typedef {function} setNative
     * @returns {any}
     * @example
     * graft.bridge.setNative(_=> {
     *     const nativeName= '네이티브 객체 이름';
     *     // IOS
     *     if(window.webkit) {
     *         return window.webkit.messageHandlers[nativeName];
     *     }
     *     // AOS
     *     if(window[nativeName]) {
     *         return window[nativeName];
     *     }
     * });
     */
    const setNative= fun=> Object.assign(_native, {self: fun? fun(): undefined});
    /**
     * {@link GraftBridge}<br>
     * 네이티브에 메시지를 전달 합니다.
     * @memberOf GraftBridge
     * @typedef {function} postMessage
     * @param {GraftBridge.message} msg 메시지 객체
     * @returns {Promise<any>} 메시지의 receive값
     * @example
     * // 네이티브 기능 요청 메시지 전달
     * graft.bridge.postMessage({
     *     command: 'cmd_sample',
     *     payload: {xxx: 1}
     * })
     * .then(receive=> {
     *     graft.log.out('요청 결과', receive);
     * });
     */
    const postMessage= msg=> {
        if(!_native.useNative()) return Promise.reject('네이트브 객체가 연결되어 있지 않습니다.');

        return new Promise((resolve, reject)=> {
            const trid= _msgMap.getTrid({resolve, reject});
            _native.postNative(Object.assign(msg, {trid}));
        });
    };
    const receiveMessage= msg=> {
        if(!msg || !msg.trid) return console.error('메시지를 확인 하세요. "trid"는 필수 입니다.', new Error);

        _msgMap.getTrigger(msg.trid).resolve(msg.receive);
    };
    /**
     * {@link GraftBridge}<br>
     * 네이티브에 메시지를 전달 합니다.
     * @memberOf GraftBridge
     * @typedef {object} mock
     * @property {function} set `(command: string, proc: (msg: {@link GraftBridge.message})=> void)=> void`<br/>
     * 네이티브 기능을 대신할 명령어를 세팅 합니다.
     * @example
     * // 네이티브 기능 모깅 처리
     * graft.bridge.mock.set('cmd_sample', msg=> {
     *     // 아래 postMessage로 전달한 메시지
     *     graft.log.out('bridge.mock::cmd_sample', msg);
     * 
     *     // 요청에 대한 결과 반환값
     *     msg.receive= {
     *         message: '네이티브 처리 결과 입니다.'
     *     };
     * 
     *     // 네이티브 수신 메소드를 호출 하여
     *     // 브릿지를 호출 한 쪽에 트리거 될 수 있도록 한다.
     *     Graft.postMessage(msg);
     * });
     * 
     * graft.exec(_=> {
     *     // 네이티브 기능 요청 메시지 전달
     *     graft.bridge.postMessage({
     *         command: 'cmd_sample',
     *         payload: {xxx: 1}
     *     })
     *     .then(receive=> {
     *         graft.log.out('요청 결과', receive);
     *     });
     * });
     */
    const mock= {
        set(command, proc) {
            if(_mock.has(command)) return console.warn('이미 세팅된 기능 입니다.', new Error);

            _mock.set(command, proc);
        },
        postMessage(msg) {
            if(!msg) return console.error('메시지를 확인 하세요.', new Error);
            if(!msg.command) return console.error('메시지를 확인 하세요. "command"는 필수 입력입니다.', new Error);

            const mock= _mock.get(msg.command);
            mock&& mock(msg);
        }
    };
    return {mock, setNative, postMessage, receiveMessage};
};
