/**
 * 로깅 처리를 관리 합니다.
 * @namespace GraftLog
 * @property {function} active `(level?:'debug')=> void`<br/>
 * 로그 출력을 활성화 합니다.<br/>
 * 👉 {@link GraftLog.active}
 * @property {function} debug `(...opt:any)=> void`<br/>
 * 디버그 로그를 출력 합니다.<br/>
 * 👉 {@link GraftLog.debug}
 * @property {function} out `(...opt:any)=> void`<br/>
 * 로그를 출력 합니다.<br/>
 * 👉 {@link GraftLog.out}
 * @property {function} getLogger `(label:string, color:string)=> (...opt:any)=> void`<br/>
 * 로거를 반환 합니다.<br/>
 * 👉 {@link GraftLog.getLogger}
 * @property {function} addDebugger `(url:string)=> void`<br/>
 * 브라우저용 디버거를 추가 합니다.<br/>
 * 👉 {@link GraftLog.addDebugger}
 */
module.exports= graft=> {
    const empty= _=>_;
    const saveKey= `[graft-log]`;
    const level= sessionStorage.getItem(saveKey);
    const layout= 'padding:0 4px 0 4px; border-radius:2px; font-weight:bold; color:#fff;';

    /**
     * {@link GraftLog}<br>
     * 로그 출력을 활성화 합니다.
     * @memberOf GraftLog
     * @typedef {function} active
     * @param {'debug'=} level 'debug' 입력 시 debug 로그 출력
     * @example
     * // 로그 활성화
     * graft.log.active();
     * // debug 레벨 활성화
     * graft.log.active('debug');
     */
    const active= level=> sessionStorage.setItem(saveKey, level);
    /**
     * {@link GraftLog}<br>
     * 디버그 로그를 출력 합니다.
     * @memberOf GraftLog
     * @typedef {function} debug
     * @param {...any} params 로그를 출력 할 값
     * @example
     * graft.log.debug('출력값');
     */
    const debug= _=> (level== 'debug')? console.log.bind(window, `%cdebug`, `${layout} background:#adb5bd;`) : empty;
    /**
     * {@link GraftLog}<br>
     * 콘솔 로그를 출력 합니다.
     * @memberOf GraftLog
     * @typedef {function} out
     * @param {...any} params 로그를 출력 할 값
     * @example
     * graft.log.out('출력값');
     */
    const out= _=> !!level? console.log.bind(window, `%cout`, `${layout} background:#6c757d;`): empty;
    /**
     * {@link GraftLog}<br>
     * 콘솔 로그를 출력 합니다.
     * @memberOf GraftLog
     * @typedef {function} getLogger
     * @param {string} label 출력 로그의 라벨
     * @param {string=} color 라벨색('info' | 'primary' | 'danger')으로 다른 색으로 할경우 코드값을 직접 입력
     * @example
     * const logger= graft.log.getLogger('lavel text');
     * logger(1, 2, 3);
     */
    const getLogger= (label, code='info')=> {
        const color= {info:'#198754;', primary:'#0d6efd', danger:'#dc3545'}[code]|| code;
        return !!level?
        console.log.bind(window, `%c${label}`, `background:${color}; ${layout}`):
        empty;
    };
    /**
     * {@link GraftLog}<br>
     * 브라우저용 디버거를 추가 합니다.
     * @memberOf GraftLog
     * @typedef {function} addDebugger
     * @param {string} url 디버거 스크립트 경로
     */
    const addDebugger= url=> {
        Graft.import(url).then(_=> {
            eruda.init(), Object.assign(result, {debug: debug(), out: out()});
        });
    };

    const result= {
        active, getLogger, addDebugger,
        debug: debug(), out: out()
    };
    return result;
};
