const tags= [
    'a','abbr','address','area','article','aside','audio',
    'b','base','bdi','bdo','blockquote','body','br','button',
    'canvas','caption','cite','code','col','colgroup',
    'data','datalist','dd','del','details','dfn','dialog','div','dl','dt',
    'em','embed',
    'fieldset','figcaption','figure','footer','form',
    'h1','h2','h3','h4','h5','h6','header','hr',
    'i','iframe','img','input','ins',
    'kbd',
    'label','legend','li','link',
    'main','map','mark','meta','meter',
    'nav','noscript',
    'object','ol','optgroup','option','output',
    'p','param','picture','pre','progress',
    'q','rp','rt','ruby',
    's','samp','section','select','small','source','span','strong','sub','summary','sup','svg',
    'table','tbody','td','template','textarea','tfoot','th','thead','time','title','tr','track',
    'u','ul',
    'var','video','wbr'
];
const _bindMethod= (graft, tag, tagName)=> {
    const {html, utils}= graft;

    /**
     * {@link GraftHtmlTag}<br/>
     * 태그 이름에 해당 하는 엘리먼트를 생성하며,
     * 전달 받은 매개변수의 타입에 별 다음과 같은 기능을 합니다.<br/>
     * **string | number**: 엘리먼트의 HTML 마크업을 랜더링 합니다.<br/>
     * **function**: 엘리먼트 생성 초기화 함수 입니다.<br/>
     * **object**: 엘리먼트 속성을 세팅 합니다.<br/>
     * **array**: 엘리먼트에 서브 엘리먼트를 추가 합니다.<br/>
     * @memberOf GraftHtmlTag
     * @typedef {function} tag
     * @param {...any} args 타입별 기능<br/>
     * **string | number**: 엘리먼트의 HTML 마크업을 랜더링 합니다.<br/>
     * **function**: 엘리먼트 생성 초기화 함수 입니다.<br/>
     * **object**: 엘리먼트 속성을 세팅 합니다.<br/>
     * **array**: 엘리먼트에 서브 엘리먼트를 추가 합니다.<br/>
     * @returns {GraftElement}
     * @example
     * // string | number: 엘리먼트의 HTML 마크업을 랜더링 합니다.
     * // 생성된 HTML: <div><strong>text</strong></div>
     * const div= graft.el.div('<strong>text</strong>');
     * 
     * // function: 엘리먼트 생성 초기화 함수 입니다.
     * // 생성된 HTML: <div>xxx</div>
     * const div= graft.el.div(_it=> {
     *     _it.lot.text= 'xxx';
     * });
     * 
     * // object: 엘리먼트 속성을 세팅 합니다. 
     * // 생성된 HTML: <div id="xx1" data-bind="xx2"></div>
     * const div= graft.el.div({id:'xx1', dataBind:'xx2'});
     * 
     * // array: 엘리먼트에 서브 엘리먼트를 추가 합니다.
     * // 생성된 HTML: <ul><li>xxx</li><li>yyy</li></ul>
     * const ul= graft.el.ul([
     *     graft.el.li('xxx'),
     *     graft.el.li('yyy'),
     * ]);
     */
    tag[tagName]= (...args)=> {
        const el= html.grafting(document.createElement(tagName));

        let children;
        args.forEach(arg=> {
            switch(true) {
            case typeof arg == 'string':
            case typeof arg == 'number':
                return Object.assign(el, {innerHTML: arg});
            case typeof arg == 'function':
                return setTimeout(_=> {
                    Object.assign(arg, {getView() {return el}});
                    arg(el);
                }, 100);
            case typeof arg == 'object':
                if(Array.isArray(arg)) children= arg;
                else {
                    Object.entries(arg).forEach(([k, v])=> el.setAttribute(utils.kebabCase(k), v))
                }
                return;
            }
        });

        // 서브 엘리먼트 추가
        children&& children.forEach(_el=> el.appendChild(_el));
        return el;
    };
};

/**
 * 엘리먼트를 생성을 위한 메소드를 제공 합니다.<br/>
 * **메소드 형식**: `{[tagname: string]: (...args: any)=> {@link GraftElement}}`<br/>
 * 메소드명에 해당 하는 엘리먼트를 생성하며,
 * 메소드의 기능은 전달 받은 매개변수의 타입별로 구분 됩니다.
 * @namespace GraftHtmlTag
 * @property {function} a `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} area `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} article `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} audio `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} b `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} br `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} button `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} canvas `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} caption `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} code `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} col `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} colgroup `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} dd `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} div `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} dl `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} dt `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} em `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} embed `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} footer `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} form `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} h1 `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} h2 `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} h3 `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} h4 `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} h5 `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} h6 `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} header `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} hr `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} i `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} iframe `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} img `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} input `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} ins `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} label `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} li `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} link `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} main `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} map `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} meta `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} nav `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} object `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} ol `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} option `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} p `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} pre `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} section `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} select `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} small `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} span `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} strong `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} sub `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} summary `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} svg `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} table `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} tbody `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} td `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} template `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} textarea `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} tfoot `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} th `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} thead `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} tr `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} u `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} ul `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 * @property {function} video `(...args: any)=> {@link GraftElement}` <br/>👉 {@link GraftHtmlTag.tag}
 */
module.exports= graft=> {
    const tag= {};
    tags.forEach(tagName=> _bindMethod(graft, tag, tagName));
    return tag;
};
