const version= '1.0.0';
const graftLog= require('./GraftLog');
const graftUtils= require('./GraftUtils');
const graftHtml= require('./GraftHtml');
const graftTag= require('./GraftHtmlTag');
const graftSuspend= require('./GraftSuspend');
const graftBridge= require('./GraftBridge');
const _graft= {
    self:undefined,
    log:undefined, utils:undefined,
    tag:undefined, html:undefined,

    suspend:undefined, bridge:undefined,
    ctrl:{}, view:{},
};

/**
 * DOM객체에 확장 된 기능을 이식 하여,
 * 바인딩된 컨트롤객체를 쉽게 핸들링 할 수 있는 라이브러리 입니다.
 * @property {GraftLog} log 로깅 처리를 관리 합니다.
 * @property {GraftUtils} utils 유틸리티 객체 입니다.
 * @property {GraftHtml} html HTML 도큐먼트를 관리 합니다.
 * @property {GraftHtmlTag} tag 엘리먼트를 생성합니다.
 * @property {GraftBridge} bridge 네이티브와 메시지를 주고 받는 역할을 합니다.
 * @property {GraftSuspend} suspend 프로세스 진행을 관리 하는 코루틴을 제공합니다.
 * @property {object} view `Object<string, {@link GraftElement}>`<br/>뷰 컬렉션
 * @property {object} ctrl `Object<string, (it: {@link GraftElement})=> void>`<br/>뷰 제어 함수 컬렉션
 */
class Graft {
    /** Graft 생성자. 싱글톤으로 동작하며, {@link Graft.getInstance}를 통해 인스턴스를 제공 받습니다. */
    constructor(proc) {
        if(_graft.self) {
            console.warn('이미 생성된 인스턴스 입니다.', new Error);
            return _graft.self;
        }

        _graft.self= this;
        _graft.log= graftLog(this);
        _graft.utils= graftUtils(this);
        _graft.suspend= graftSuspend(this);
        _graft.bridge= graftBridge(this);
        _graft.html= graftHtml(this);
        _graft.tag= graftTag(this);

        proc && proc(this);
    }
    static get version() { return version }
    /**
     * Graft 인스턴스를 반환 합니다.
     * @param {function=} proc (self: {@link Graft})=> void<br/>
     * 객체 생성시 호출 되는 함수, 매개변수로 생성된 객체의 인스턴스를 전달 합니다.
     * @returns {Graft}
     * @example
     * const graft= Graft.getInstance(graft=> {
     *     graft.isMobile= /android|iphone/i.test(navigator.userAgent);
     *     graft.log.out('객체 초기화 및 환경 설정');
     * });
     */
    static getInstance(proc) { return new Graft(proc) }
    /**
     * 자바스크립트 모듈을 추가 합니다.
     * @param {string| Array<string>} url 스크립트 URL정보, 다건 추가시 배열로 입력
     * @returns {Promise<void>}
     * @example
     * // xxx.js 로드 완료 후
     * // yyy.js, zzz.js 추가
     * Graft.import('xxx.js')
     * .then(_=> Graft.import([
     *     'yyy.js',
     *     'zzz.js'
     * ]));
     */
    static import(url) {
        const now= new Date();
        const urls= Array.isArray(url)? url : [url];

        return Promise.all(urls.map(url=> _loadScript(url)))
        .then(_=> new Promise(resolve=> {
            requestAnimationFrame(_=> resolve());
        }));

        function _loadScript(url) {
            const script= document.createElement('script');
            return new Promise(resolve=> {
                if(!url) return resolve();

                script.src= `${url}${/\?/.test(url)? '': `?${now.getTime()}`}`;
                script.onload= _=> resolve();
                script.onerror= _=> resolve(url);
                document.head.appendChild(script);
            });
        }
    }
    /**
     * 네이티브 부터 메시지를 수신 합니다.
     * @param {GraftBridge.message} msg 메시지 구조체
     */
    static postMessage(msg) { _graft.bridge.receiveMessage(msg) }

    get log() { return _graft.log }
    get utils() { return _graft.utils }
    get suspend() { return _graft.suspend }
    get bridge() { return _graft.bridge }
    get tag() { return _graft.tag }
    get html() { return _graft.html }
    get ctrl() {  return _graft.ctrl }
    get view() {  return _graft.view }

    /**
     * 어플리케이션의 고유 프로세스를 진행 할 수 있는 기능을 제공 합니다.
     * @param {function} proc (self: {@link Graft})=> void<br/>
     * 수행 프로시저로 매개변수로 생성된 객체의 인스턴스를 전달 합니다.
     * @example
     * graft.exec(graft=> {
     *     graft.log.out('프로세스 진행');
     * });
     */
    exec(proc) {
        if(typeof proc != 'function') {
            throw '전달값을 확인 하세요.| 실행 프로시저가 존재하지 않거나 함수가 아닙니다.';
        }
        proc(this);
    }
    /**
     * 설정 시간 동안 대기 후 생성된 인스턴스를 반환 합니다.
     * @param {number=} wait 대기시간을 밀리초단위로 입력
     * @returns {Promise<Graft>}
     */
    build(wait= 50) {
        return new Promise(resolve=> {
            setTimeout(_=> resolve(this), wait);
        });
    }
};

/**
 * 확장기능
 * @namespace GraftExtends
 * @property {object} popup 팝업 호출을 관리 합니다.
 */
window.Graft= Graft;
