const _upperToDash= (vl, replace)=> vl.replace(/([a-z0-9])([A-Z])/g, replace);
const _dashToUpper= vl=> {
    if(!/[-_]/g.test(vl)) return vl;
    else {
        return vl.toLowerCase()
        .replace(/([_-]\w)/g, v => v[1].toUpperCase())
        .replace(/_/g, '');
    }
};

/**
 * 유틸리티
 * @namespace GraftUtils
 * @property {function} clone `(vl: any)=> any`<br/>
 * 값의 복사본을 반환 합니다.<br/>
 * 👉 {@link GraftUtils.clone}
 * @property {function} kebabCase `(vl: string, toUpper?: boolean)=> string`<br/>
 * 대상 문자열을 케밥(xxx-xxx) 케이스로 변환 합니다.<br/>
 * 👉 {@link GraftUtils.kebabCase}
 * @property {function} snakeCase `(vl: string, toUpper?: boolean)=> string`<br/>
 * 대상 문자열을 스네이크(xxx_xxx) 케이스로 변환 합니다.<br/>
 * 👉 {@link GraftUtils.snakeCase}
 * @property {function} camelCase `(vl: string)=> string`<br/>
 * 대상 문자열을 카멜(xxxXxx) 케이스로 변환 합니다.<br/>
 * 👉 {@link GraftUtils.camelCase}
 * @property {function} pascalCase `(vl: string)=> string`<br/>
 * 대상 문자열을 파스칼(XxxXxx) 케이스로 변환 합니다.<br/>
 * 👉 {@link GraftUtils.pascalCase}
 */
module.exports= _graft=> {
    /**
     * {@link GraftUtils}<br>
     * 카멜/파스칼 케이스를 케밥(xxx-xxx) 케이스로 변환 합니다.
     * @memberOf GraftUtils
     * @typedef {function} kebabCase
     * @param {string} vl 변환 대상 문자열
     * @param {boolean=} toUpper 대/소문자 구분으로 미입력시 소문자로 변환(기본값: false)
     * @returns {string}
     */
    const kebabCase= (vl, toUpper=false)=> {
        const result= _upperToDash(vl, '$1-$2');
        return toUpper? result.toUpperCase(): result.toLowerCase();
    };
    /**
     * {@link GraftUtils}<br>
     * 카멜/파스칼 케이스를 스네이크(xxx_xxx) 케이스로 변환 합니다.
     * @memberOf GraftUtils
     * @typedef {function} snakeCase
     * @param {string} vl 변환 대상 문자열
     * @param {boolean=} toUpper 대/소문자 구분으로 미입력시 소문자로 변환(기본값: false)
     * @returns {string}
     */
    const snakeCase= (vl, toUpper=false)=> {
        const result= _upperToDash(vl, '$1_$2');
        return toUpper? result.toUpperCase(): result.toLowerCase();
    }
    /**
     * {@link GraftUtils}<br>
     * 스네이크/케밥 케이스를 카멜(xxxXxx) 케이스로 변환 합니다.
     * @memberOf GraftUtils
     * @typedef {function} camelCase
     * @param {string} vl 변환 대상 문자열
     * @returns {string}
     */
    const camelCase= vl=> _dashToUpper(vl).replace(/^[\w]/, v=> v.toLowerCase());
    /**
     * {@link GraftUtils}<br>
     * 스네이크/케밥 케이스를 파스칼(XxxXxx) 케이스로 변환 합니다.
     * @memberOf GraftUtils
     * @typedef {function} pascalCase
     * @param {string} vl 변환 대상 문자열
     * @returns {string}
     */
    const pascalCase= vl=> _dashToUpper(vl).replace(/^[\w]/, v=> v.toUpperCase());

    /**
     * {@link GraftUtils}<br>
     * 값의 복사본을 반환 합니다.
     * @memberOf GraftUtils
     * @typedef {function} clone
     * @param {any} vl 
     * @returns {any}
     */
    const clone= vl=> {
        if(Array.isArray(vl)) return vl.slice(0);
        if(typeof vl == 'object') return Object.assign({}, vl);
        return vl;
    };
    return {
        clone,
        kebabCase, snakeCase, camelCase, pascalCase,
    };
};
