/**
 * {@link GraftElement}<br>
 * 엘리먼트를 핸들링 할 수 있는 기능 묶음 입니다.
 * @memberOf GraftElement
 * @typedef {object} lot
 * @property {any} data 사용자 정의 데이터를 저장 합니다.
 * @property {string} text 엘리먼트의 TEXT 입/출력을 처리 합니다.<br/>
 * 👉 {@link GraftElement.lotText}
 * @property {string} html 엘리먼트의 HTML 마크업을 랜더링을 처리 합니다.<br/>
 * 👉 {@link GraftElement.lotHtml}
 * @property {string| Array<string>} value 입력 컨트롤의 value 값을 처리 합니다.
 * 체크박스 인경우 동일 이름(`name`) 속성의 값(`value`)을 배열로 반환 합니다.<br/>
 * 👉 {@link GraftElement.lotValue}
 * @property {object} css 엘리먼트의 클래스를 관리 합니다.<br/>
 * 👉 {@link GraftElement.lotCss}
 * @property {function} insert `(chile: {@link GraftElement})=> {@link GraftElement}`<br/>
 * 엘리먼트(child) 객체를 삽입 합니다.<br/>
 * 👉 {@link GraftElement.lotInsert}
 * @property {function} truncate `()=> {@link GraftElement.lot}`<br/>
 * 엘리먼트를 비웁니다.<br/>
 * 👉 {@link GraftElement.lotTruncate}
 * @property {function} event `(type: string, listener: (e: Event)=> any)=> void`<br/>
 * 이벤트를 등록 합니다.<br/>
 * 👉 {@link GraftElement.lotEvent}
 * @property {function} removeEvent `(type: string)=> void`<br/>
 * 등록된 이벤트를 삭제 합니다.<br/>
 * 👉 {@link GraftElement.lotRemoveEvent}
 * @property {function} dataBind `()=> {@link GraftElement.lotDataBindMap}`<br/>
 * `data-bind` 지시자로 등록된 객체를 반환 합니다.<br/>
 * 👉 {@link GraftElement.lotDataBind}
 */
const getGraftLot= (graft, el)=> {
    /**
     * 엘리먼트 객체(HTMLElement)와 엘리먼트의 핸들링 객체({@link GraftElementLot})의 믹스된 객체 입니다.
     * @namespace GraftElement
     * @property {GraftElement.lot} lot 엘리먼트를 핸들링 할 수 있는 기능 묶음 입니다.
     */
    const lot= {
        // 이벤트 리스터 콜렉터
        _eventMap: new Map,
    };

    Object.defineProperty(lot, 'text', _lotText(el));
    Object.defineProperty(lot, 'html', _lotHtml(el));
    Object.defineProperty(lot, 'value', _lotValue(el));
    lot.data= undefined;
    lot.css= _lotCss(el);
    lot.insert= (chile, reference)=> _lotInsert(chile, reference, el);
    lot.truncate= _=> _lotTruncate(el, lot);
    lot.event= (type, listener)=> _lotEvent(type, listener, el, lot, graft);
    lot.removeEvent= type=> _lotRemoveEvent(type, el, lot);
    lot.dataBind= _=> _dataBind(el, graft);

    return lot;
};
/**
 * {@link GraftElement}<br>
 * `()=> {@link GraftElement.lotDataBindMap}`<br/>
 * `data-bind` 지시자로 등록된 객체를 반환 합니다.
 * @memberOf GraftElement
 * @typedef {function} lotDataBind
 * @example
 * // <div class="form">
 * //   <input data-bind="input" type="text">
 * //   <button data-bind="submit" class="btn">확인</button>
 * //   <div data-bind="output" class="txtBox"></div>
 * // </div>
 * const form= graft.tag.div(_it=> {
 *     // 바인딩된 엘리먼트를 반환 받음
 *     const vo= _it.lot.dataBind();
 * 
 *     // 버튼 클릭 이벤트 등록
 *     vo.submit.lot.event('click', e=> {
 *         // input의 입력값을 output에 출력
 *         vo.output.lot.text= vo.input.lot.value;
 *     });
 * }, {class:'form'}, [
 *     graft.tag.input({dataBind:'input', type:'text'}),
 *     graft.tag.button('확인', {dataBind:'submit', class:'btn'}),
 *     graft.tag.div({dataBind:'output', class:'txtBox'}),
 * ]);
 */
const _dataBind= (el, {log})=> {
    const vo= [...el.querySelectorAll('[data-bind]')]
    .reduce((vo, _el)=> {
        const {dataset:{bind}}= _el;
        return vo[bind]? vo: Object.assign(vo, {[bind]: _el});
    }, {});

    /**
     * {@link GraftElement}<br>
     * `data-bind` 지시자로 등록된 엘리먼트 콜렉션 객체
     * @memberOf GraftElement
     * @typedef {object} lotDataBindMap
     * @property {function} setText `(vl: Object<string, string>)=> void`<br/>
     * 해당 엘리먼트의 TEXT를 변경 합니다.
     * @property {function} getText `()=> Object<string, string>`<br/>
     * 엘리먼트의 TEXT를 {key: value} 형태의 객체로 반환 합니다.
     * @property {function} setHtml `(vl: Object<string, string>)=> void`<br/>
     * 해당 엘리먼트의 HTML 마크업을 변경 합니다.
     * @property {function} getHtml `()=> Object<string, string>`<br/>
     * 엘리먼트의 HTML를 {key: value} 형태의 객체로 반환 합니다.
     * @property {function} setValue `(vl: Object<string, string | Array<string>>)=> void`<br/>
     * 해당 엘리먼트의 VALUE를 변경 합니다.
     * @property {function} getValue `()=> Object<string, string | Array<string>>`<br/>
     * 엘리먼트의 VALUE를 {key: value} 형태의 객체로 반환 합니다.
     * @example
     * graft.tag.div(_it=> {
     *     // 바인딩된 엘리먼트를 반환 받음
     *     const vo= _it.lot.dataBind();
     * 
     *     // Text 변경 및 취득
     *     vo.setText({...});
     *     const text= vo.getText();
     * });
     */
    const binder= Object.create(vo);
    binder.setText= vl=> {
        if(typeof vl != 'object') {
            return log.debug('입력값을 객체로 전달 해주세요.', new Error);
        }

        Object.entries(vl).forEach(([ky, text])=> {
            vo[ky]&& Object.assign(vo[ky].lot, {text});
        });
    };
    binder.getText= _=> {
        return Object.entries(vo).reduce((result, [ky, el])=> {
            return Object.assign(result, {[ky]: el.lot.text});
        }, {});
    };
    binder.setHtml= vl=> {
        if(typeof vl != 'object') {
            return log.debug('입력값을 객체로 전달 해주세요.', new Error);
        }

        Object.entries(vl).forEach(([ky, html])=> {
            vo[ky]&& Object.assign(vo[ky].lot, {html});
        });
    };
    binder.getHtml= _=> {
        return Object.entries(vo).reduce((result, [ky, el])=> {
            return Object.assign(result, {[ky]: el.lot.html});
        }, {});
    };
    binder.setValue= vl=> {
        if(typeof vl != 'object') {
            return log.debug('입력값을 객체로 전달 해주세요.', new Error);
        }

        Object.entries(vl).forEach(([ky, value])=> {
            vo[ky]&& Object.assign(vo[ky].lot, {value});
        });
    };
    binder.getValue= _=> {
        return Object.entries(vo).reduce((result, [ky, el])=> {
            return Object.assign(result, {[ky]: el.lot.value});
        }, {});
    };

    return binder;
};
/**
 * {@link GraftElement}<br>
 * `(type: string)=> void`<br/>
 * 등록된 이벤트를 삭제 합니다.
 * @memberOf GraftElement
 * @typedef {function} lotRemoveEvent
 * @param {string} type 이벤트 타입
 * @example
 * const submit= graft.tag.button('submit');
 * 
 * // 클릭 이벤트 등록
 * submit.lot.event('click', e=> {
 *     graft.log.out('버튼클릭');
 * });
 * // 클릭 이벤트 등록
 * submit.lot.event('click.xxx', e=> {
 *     // 클릭 이벤트 제거
 *     submit.log.removeEvent('click');
 * });
 */
const _lotRemoveEvent= (typeFlag, el, {_eventMap})=> {
    if(!_eventMap.has(typeFlag)) return;

    const [type]= typeFlag.split('.');
    if(!type) return;

    el.removeEventListener(type, _eventMap.get(typeFlag));
    _eventMap.delete(typeFlag);
};
/**
 * {@link GraftElement}<br>
 * `(type: string, listener: (e: Event)=> any)=> void`<br/>
 * 이벤트를 등록 합니다.
 * @memberOf GraftElement
 * @typedef {function} lotEvent
 * @param {string} typeFlag 이벤트 타입
 * @param {function} listener `(e: Event)=> any`<br/>
 * 이벤트 리스너(콜백 함수)
 * @example
 * const submit= graft.tag.button('submit');
 * 
 * // 클릭 이벤트 등록
 * submit.lot.event('click', e=> {
 *     graft.log.out('버튼클릭');
 * });
 * // 클릭 이벤트 등록
 * submit.lot.event('click.xxx', e=> {
 *     // 클릭 이벤트 제거
 *     submit.log.removeEvent('click');
 * });
 */
const _lotEvent= (typeFlag, listener, el, {_eventMap}, {log})=> {
    if(_eventMap.has(typeFlag)) return log.debug(`중복 등록된 이벤트(${typeFlag}) 입니다.`, new Error);

    const [type]= typeFlag.split('.');
    if(!type) return;

    el.addEventListener(type, listener);
    _eventMap.set(typeFlag, listener);
};
/**
 * {@link GraftElement}<br>
 * `()=> {@link GraftElement.lot}`<br/>
 * 엘리먼트를 비웁니다.
 * @memberOf GraftElement
 * @typedef {function} lotTruncate
 * @example
 * const div= graft.tag.div('xxx');
 * div.lot.truncate();
 */
const _lotTruncate= (el, lot)=> {
    el.innerHTML= '';
    return lot;
};
/**
 * {@link GraftElement}<br>
 * 엘리먼트 객체를 삽입 합니다.
 * @memberOf GraftElement
 * @typedef {function} lotInsert
 * @param {GraftElement} chile 삽입 할 엘리먼트 객체
 * @param {GraftElement=} reference 전달한 엘리먼트 앞에 삽입
 * @returns {GraftElement}
 * @example
 * // <div>xxx</div>
 * const div= graft.tag.div('xxx');
 * 
 * // 엘리먼트 삽입
 * // <div>xxx<div>yyy</div></div>
 * div.lot.insert(graft.tag.div('yyy'));
 */
const _lotInsert= (child, reference, target)=> {
    if(reference) return target.insertBefore(child, reference);
    else {
        return target.appendChild(child);
    }
};
/**
 * {@link GraftElement}<br>
 * 엘리먼트의 클래스를 관리 합니다.
 * @memberOf GraftElement
 * @typedef {object} lotCss
 * @property {function} has `(name: string)=> boolean`<br/>
 * 클래스가 존재 하는지 검색 합니다.
 * @property {function} add `(name: string | Array<string>)=> {@link GraftElement.lotCss}`<br/>
 * 클래스를 추가 합니다. 다건인 경우 배열로 전달 합니다.
 * @property {function} remove `(name: string | Array<string>)=> {@link GraftElement.lotCss}`<br/>
 * 클래스를 삭제 합니다. 다건인 경우 배열로 전달 합니다.
 * @example
 * const btnToggle= graft.tag.button('btnToggle', {class: 'active'});
 * 
 * btnToggle.lot.event('click', e=> {
 *     const css= btnToggle.lot.css;
 * 
 *     // 클래스명 조회하여 토글 기능 구현
 *     if(css.has('active')) css.add('active');
 *     else {
 *         css.remove('active');
 *     }
 * });
 */
const _lotCss= el=> {
    const {classList}= el;
    return {
        has(name) { return classList.contains(name) },
        remove(name) {
            _toArray(name).forEach(name=> classList.remove(name));
            return this;
        },
        add(name) {
            _toArray(name).forEach(name=> classList.add(name));
            return this;
        }
    };
};
/**
 * {@link GraftElement}<br>
 * 입력 컨트롤의 value 값을 처리 합니다.</br>
 * 체크박스 인경우 동일 이름(`name`) 속성의 값(`value`)을 배열로 반환 합니다.
 * @memberOf GraftElement
 * @typedef {string| Array<string>} lotValue
 * @example
 * // <input type="text" value="xxx">
 * const input= graft.tag.input({type:'text', value:'xxx'});
 * // 출력값: xxx
 * graft.log.out('value: ', div.lot.value);
 * // <input type="text" value="yyy">
 * input.lot.value= 'yyy';
 * 
 * // 체크박스 처리
 * const check1= graft.tag.input({type:'checkbox', name:'check', value:'xxx', checked:'checked'});
 * const check2= graft.tag.input({type:'checkbox', name:'check', value:'yyy', checked:'checked'});
 * const form= graft.tag.div([
 *     check1, check2,
 * ]);
 * 
 * // 출력값: ['xxx', 'yyy']
 * graft.log.out('value: ', check1.lot.value);
 * // value="yyy"의 체크박스만 선택됨
 * check1.lot.value= ['yyy'];
 */
const _lotValue= el=> {
    return {
        get() {
            const {value, type}= el;
            if(value == undefined) return '';

            if(!/checkbox|radio/i.test(type)) return value;
            else {
                const result= _getCheckedElement(el, true).map(el=> el.value);
                return /checkbox/i.test(type)? result: result.join('');
            }
        },
        set(vl) {
            const {value, type}= el;
            if(value == undefined) return;

            if(!/checkbox|radio/i.test(type)) el.innerHTML= vl;
            else {
                const values= _toArray(vl);
                _getCheckedElement(el).forEach(el=> el.checked= values.includes(el.value));
            }
        },
    };
};
/**
 * {@link GraftElement}<br>
 * 엘리먼트의 HTML 마크업을 랜더링 처리 합니다.
 * @memberOf GraftElement
 * @typedef {string} lotHtml
 * @example
 * // <div><strong>xxx</strong></div>
 * const div= graft.tag.div([
 *     graft.tag.strong('xxx'),
 * ]);
 * // 출력값: <strong>xxx</strong>
 * graft.log.out('html: ', div.lot.html);
 * // <div><p>yyy</p></div>
 * div.lot.html= '<p>yyy</p>';
 */
const _lotHtml= el=> {
    return {
        get() { return el.innerHTML },
        set(vl) { el.innerHTML= vl },
    };
};
/**
 * {@link GraftElement}<br>
 * 엘리먼트의 TEXT 입/출력을 처리 합니다.
 * @memberOf GraftElement
 * @typedef {string} lotText
 * @example
 * // <div>xxx</div>
 * const div= graft.tag.div('xxx');
 * // 출력값: xxx
 * graft.log.out('text: ', div.lot.text);
 * // <div>yyy</div>
 * div.lot.text= 'yyy';
 */
const _lotText= el=> {
    return {
        get() { return el.textContent },
        set(vl) { el.textContent= vl },
    };
};
const _toArray= v=> (Array.isArray(v)? v: [v]);
const _getCheckedElement= (el, isChecked)=> {
    const {name}= el;
    if(name) return [...document.querySelectorAll(`input[name=${name}]${isChecked? ':checked': ''}`)];
    else {
        return el.checked? [el]: [];
    }
};

/**
 * HTML 도큐먼트를 관리 합니다.
 * @namespace GraftHtml
 * @property {function} grafting `(el: HTMLElement)=> {@link GraftElement}`<br/>
 * 엘리먼트와 핸들링 객체를 믹스인 합니다.<br/>
 * 👉 {@link GraftHtml.grafting}
 * @property {function} insert `(chile: {@link GraftElement}, reference?: {@link GraftElement})=> {@link GraftElement}`<br/>
 * 도큐먼트 body에 엘리먼트를 삽입 합니다.<br/>
 * 👉 {@link GraftHtml.insert}
 * @property {function} query `(selector: string, target?: {@link GraftElement})=> {@link GraftElement}`<br/>
 * 선택자와 일치 하는 첫번째 엘리먼트를 반환 합니다.<br/>
 * 👉 {@link GraftHtml.query}
 * @property {function} queryAll `(selector: string, target?: {@link GraftElement})=> Array<{@link GraftElement}>`<br/>
 * 선택자와 일치 하는 모든 엘리먼트를 배열로 반환 합니다.<br/>
 * 👉 {@link GraftHtml.queryAll}
 */
module.exports= graft=> {
    /**
     * {@link GraftHtml}<br>
     * 엘리먼트와 핸들링 객체를 믹스인 합니다.
     * @memberOf GraftHtml
     * @typedef {function} grafting
     * @param {HTMLElement} el 믹스인 대상 엘리먼트
     * @returns {GraftElement}
     */
    const grafting= el=> {
        if(el.lot) return el;

        const lot= getGraftLot(graft, el);
        return Object.assign(el, {lot});
    };
    /**
     * {@link GraftHtml}<br>
     * 도큐먼트 body에 엘리먼트를 삽입 합니다.
     * @memberOf GraftHtml
     * @typedef {function} insert
     * @param {GraftElement} chile 삽입 할 엘리먼트 객체
     * @param {GraftElement=} reference 전달한 엘리먼트 앞에 삽입
     * @returns {GraftElement}
     * @example
     * // <div>
     * //   <header></header>
     * //   <main></main>
     * // </div>
     * const main= graft.html.insert(graft.el.main());
     * graft.html.insert(graft.el.header(), main);
     */
    const insert= (child, reference)=> _lotInsert(child, reference, document.body);

    const query= (selector, target=document)=> {
        return target.querySelector(selector);
    };
    const queryAll= (selector, target=document)=> {
        return [...target.querySelectorAll(selector)];
    };
    return {grafting, insert, query, queryAll};
};
